.container {
    padding-left: 20px;
    padding-right: 20px;
    margin-left: auto;
    margin-right: auto;
}

@media screen and (min-width: $mobile) {
    .container {
        width: 480px;
    }
}

@media screen and (min-width: $tablet) {
    .container {
        padding-left: 32px;
        padding-right: 32px;
        width: 768px;
    }
}

@media screen and (min-width: $desktop) {
    .container {
        padding-left: 16px;
        padding-right: 16px;
        width: 1199px;
    }
}

@media screen and  (min-width: $largest-desktop )  {
    .container{
        padding-left: 16px;
        padding-right: 16px;
        width: 1399px;
    }
}